














import Vue from "vue";
import {
  epochMsToDate,
  epochMsToRelTimeString
} from "@/vue/filters/DateFilters";

import { cardRepository } from "../../util/Repositories";

export default Vue.extend({
  filters: {
    epochMsToDate: epochMsToDate,
    epochMsToRelTimeString: epochMsToRelTimeString
  },
  methods: {
    clearLocalStorage: function(cardId: string) {
      window.localStorage.removeItem(cardId);
      this.$data.localStoredCard = null;
    }
  },
  data: function() {
    const card = cardRepository.find(this.$route.params.id);
    return {
      card: card,
      localStoredCard: window.localStorage.getItem(card.id)
    };
  }
});
