/*
 * Converts an epoch time number to a human readable date
 */

const epochMsToDate = function(value: number) {
  return new Date(value).toTimeString();
};

/*
 * Converts an epoch time number to a human readable time delta. current time is the current time in epoch millis
 */

const epochMsToRelTimeString = function(value: number) {
  const now = new Date().getTime();
  return `${(value - now) / 1000 / 60} min`;
};

export { epochMsToDate, epochMsToRelTimeString };
